import React from 'react'
import './AboutPage.css';
import Hero from '../hero/hero';
import { Grid } from '@mui/material';
import Revital from './Revital.jpg'
import Sima from './Sima.jpg'
import RevitalAndSima from './   .jpg'
import { useMediaQuery } from 'react-responsive';

const AboutPage = (props) => {
    
    const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });


    return(
        isDesktop?
       <Hero>
            <Grid container>
                <Grid item xs = {1}></Grid>
                <Grid container item xs = {4} style = {{marginTop:'4vh'}} spacing={1}>
                    <Grid item xs = {12}>
                        <img src = {RevitalAndSima} style={{width : '100%', objectFit:'contain'}}></img>
                    </Grid>
                    <Grid item xs = {6}>
                        <img src = {Sima} style={{width : '100%', objectFit:'contain'}}></img>
                    </Grid>
                    <Grid item xs = {6}>
                        <img src = {Revital} style={{width : '100%', objectFit:'contain'}}></img>
                    </Grid> 
                </Grid>
                <Grid item xs = {1.5}></Grid>
                <Grid container item xs = {5} style = {{justifyContent:'right',marginTop:'4vh'}}>
                    <p className='AboutHeader'>   -</p>
                    <p className='AboutSubtext'>          <br></br>
                           ! <br></br>
      <br></br>
             .<br></br>
       
         ,
        ,
        ,  .

    .   , .
        .
  ,   ,   
      .<br></br>
                 -70       !

             
    !<br></br>
           .<br></br>
   ,        . 
</p>
                </Grid>
                <Grid item xs = {0.5}></Grid>
            </Grid>
       </Hero>
       :
       <Hero>
       <Grid container>
           <Grid item xs = {1}></Grid>
           <Grid container item xs = {10} style = {{justifyContent:'right',marginTop:'4vh'}}>
               <p className='AboutHeaderMobile'>   -</p>
               <p className='AboutSubtextMobile'>          
                      ! <br></br><br></br>
      
            .
     
      ,
    ,
   ,  .<br></br><br></br></p>

<p className='AboutHeaderMobile'>   ?</p>
<p className='AboutSubtextMobile'> 
    .   , .
        .
  ,   ,   
     .
              -70       !

             
   !
           .
  ,        . 
</p>
           </Grid>
           <Grid item xs = {0.5}></Grid>

           <Grid item xs = {1}></Grid>
           <Grid container item xs = {10} style = {{marginTop:'3vh', marginBottom : '3vh'}} spacing={1}>
               <Grid item xs = {12}>
                   <img src = {RevitalAndSima} style={{width : '100%', objectFit:'contain'}}></img>
               </Grid>
               <Grid item xs = {6}>
                   <img src = {Sima} style={{width : '100%', objectFit:'contain'}}></img>
               </Grid>
               <Grid item xs = {6}>
                   <img src = {Revital} style={{width : '100%', objectFit:'contain'}}></img>
               </Grid> 
           </Grid>
           <Grid item xs = {1}></Grid>
       </Grid>
        </Hero>
    )
}

export default AboutPage

