import React from 'react'
import './questionsPage.css';
import Hero from '../hero/hero';
import { Grid } from '@mui/material';
import { styled } from '@mui/material/styles';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import DetailsButton from '../DetailsButton/DetailsButton'
import { useMediaQuery } from 'react-responsive';

const QuestionsPage = (props) => {
    const Accordion = styled((props) => (
        <MuiAccordion disableGutters elevation={0} square {...props} />
      ))(({ theme }) => ({
        borderBottom: `1px solid #124770`,
        '&:before': {
          display: 'none',
          
        },
      }));
      
      const AccordionSummary = styled((props) => (
        <MuiAccordionSummary
          expandIcon={<AddCircleIcon sx={{ fontSize: '1rem'}} />}
          {...props}
        />
      ))(({ theme }) => ({
        backgroundColor:'rgba(255, 255, 255)',
        color : '#124770',
       marginTop:'3vh',
        flexDirection: 'row',
        '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
          transform: 'rotate(90deg)',
        },
        '& .MuiAccordionSummary-content': {
          marginLeft: theme.spacing(1),
          direction : "rtl",
        
        },
      }));
      
      const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
        padding: theme.spacing(2),
        borderTop: '1px solid #124770',
        color: '#124770',
        direction : 'rtl',
      }));
      
        const [expanded, setExpanded] = React.useState('panel1');
      
        const handleChange = (panel) => (event, newExpanded) => {
          setExpanded(newExpanded ? panel : false);
        };
    
    let questionsVar = [["      ?","    ."], 
    ["        .  ?", " .   '            . '     . "]
    ,["         ,   ?","       .             ,   ,     .                . "],
    ["          ?    "," .    "],["         ?    "," !    "]]


    const isDesktop = useMediaQuery({
      query: "(min-device-width: 1200px)",
  });


    return(
      isDesktop?
       <Hero >
            <Grid container>
              <Grid item xs = {12}>
                    <p className='questionsHeader'>     ? <br></br><span className='questionsHeaderHand'>  !</span></p>
              </Grid>
              <Grid item xs = {2}></Grid>
              <Grid item xs = {8}>
                            <div>
                                {questionsVar.map((question, index) => {
                                    return(
                                        <Accordion>
                                        <AccordionSummary
                                        expandIcon={<ExpandMoreIcon style = {{color : '#124770'}}/>}
                                        aria-controls="panel1a-content"
                                        id="panel1a-header"
                                        >
                                        <Typography style = {{fontSize : '1.2vw', fontWeight : '600', fontFamily: 'Segoe UI'}}>{question[0]}</Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                        <Typography style = {{color : '#124770', fontSize : '1vw', marginRight:'1.2vw', fontFamily: 'Segoe UI'}}>
                                            {question[1]}
                                        </Typography>
                                        </AccordionDetails>
                                        </Accordion>      
                                    )
                                })}                                     
                             </div>
              </Grid>
              <Grid item xs = {1}></Grid>
              <Grid item xs = {4}></Grid>
              <Grid container item xs = {4} style = {{justifyContent : 'center', marginTop:'10vh'}}>
                <DetailsButton  text = "!    "></DetailsButton>
              </Grid>

            </Grid>
       </Hero>
       :
       <Hero>
       <Grid container>
         <Grid item xs = {12}>
               <p className='questionsHeaderMobile'>     ? <br></br><span className='questionsHeaderHandMobile'>  !</span></p>
         </Grid>
         <Grid item xs = {0.5}></Grid>
         <Grid item xs = {11}>
                       <div>
                           {questionsVar.map((question, index) => {
                               return(
                                   <Accordion>
                                   <AccordionSummary
                                   expandIcon={<ExpandMoreIcon style = {{color : '#124770'}}/>}
                                   aria-controls="panel1a-content"
                                   id="panel1a-header"
                                   >
                                   <Typography style = {{fontSize : '2vh', fontWeight : '600'}}>{question[0]}</Typography>
                                   </AccordionSummary>
                                   <AccordionDetails>
                                   <Typography style = {{color : '#124770', fontSize : '2vh', marginRight:'1.2vw'}}>
                                       {question[1]}
                                   </Typography>
                                   </AccordionDetails>
                                   </Accordion>      
                               )
                           })}                                     
                        </div>
         </Grid>
         <Grid item xs = {0.5}></Grid>
         <Grid container item xs = {12} style = {{justifyContent : 'center', marginTop:'3vh', marginBottom : '3vh'}}>
           <DetailsButton></DetailsButton>
         </Grid>

          </Grid>
      </Hero>
    )
}

export default QuestionsPage