import React from 'react'
import Hero from '../hero/hero'
import './whosPage.css'
import { Grid } from '@mui/material'
import WhosCube from './whosCube/whosCube'
import { useMediaQuery } from 'react-responsive';

const WhosPage = (props) => {
    const CubesValues = [["#ADDEDB"," " , "         ,      "],
    ["#FFD459"," " , "            "],
    ["#EDEDE6"," " , ",          "],
    ["#F57854","" , "        ,        "],
    ["#124770"," " , "         ", true],["#FCCC9E"," " , "       "]]

    const isDesktop = useMediaQuery({
      query: "(min-device-width: 1200px)",
    });

    return(
      isDesktop?
      <Hero style = {{display : 'flex', justifyContent:'center'}}>
        <p className='whosHeader'>        -</p> 
        <Grid container>
            <Grid item xs = {2}></Grid>
            <Grid item xs = {8} style = {{height:'81vh', marginTop:'4vh'}}>
                <Grid container style={{textAlign:'center'}}>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[0][0]} header = {CubesValues[0][1]} subtext = {CubesValues[0][2]}></WhosCube></Grid>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[1][0]} header = {CubesValues[1][1]} subtext = {CubesValues[1][2]}></WhosCube></Grid>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[2][0]} header = {CubesValues[2][1]} subtext = {CubesValues[2][2]}></WhosCube></Grid>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[3][0]} header = {CubesValues[3][1]} subtext = {CubesValues[3][2]}></WhosCube></Grid>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[4][0]} header = {CubesValues[4][1]} subtext = {CubesValues[4][2]} blue = {CubesValues[4][3]}></WhosCube></Grid>
                    <Grid item xs = {4} style = {{height:'40.5vh'}}><WhosCube backgroundColor = {CubesValues[5][0]} header = {CubesValues[5][1]} subtext = {CubesValues[5][2]}></WhosCube></Grid>
                </Grid>
            </Grid>
            <Grid item xs = {2}></Grid>
        </Grid>
      </Hero>
      :
      <Hero >
      <p className='whosHeaderMobile'>        -</p> 
      <Grid container>   
          <Grid item xs = {12}>
              <Grid container style={{textAlign:'center'}}>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[0][0]} header = {CubesValues[0][1]} subtext = {CubesValues[0][2]}></WhosCube></Grid>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[1][0]} header = {CubesValues[1][1]} subtext = {CubesValues[1][2]}></WhosCube></Grid>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[2][0]} header = {CubesValues[2][1]} subtext = {CubesValues[2][2]}></WhosCube></Grid>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[3][0]} header = {CubesValues[3][1]} subtext = {CubesValues[3][2]}></WhosCube></Grid>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[4][0]} header = {CubesValues[4][1]} subtext = {CubesValues[4][2]} blue = {CubesValues[4][3]}></WhosCube></Grid>
                  <Grid item xs = {12} style = {{height:'40vh'}}><WhosCube backgroundColor = {CubesValues[5][0]} header = {CubesValues[5][1]} subtext = {CubesValues[5][2]}></WhosCube></Grid>
              </Grid>
          </Grid>      
        </Grid>
      </Hero>
    )
}

export default WhosPage